import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { AngularFireAuth } from '@angular/fire/auth';
import { LoadingController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import  { Observable }  from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  isuser: boolean;
  username;
  password;
  page_name;
  user_found:boolean;
  is_login;
  comments: AngularFireList<any>;
  ncom:Observable<any[]>;
  items: Observable<any[]>;


  
  

  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public db: AngularFireDatabase, public afAuth: AngularFireAuth,public loadingCtrl: LoadingController) {
    this.is_login = true;
    this.user_found=false;
    this.page_name="Login";
  

  }
  swipe(event) {
    if(event.direction === 2) {
      this.navCtrl.parent.select(1);
    }
  }

  display(){

      this.user_found=true;
      this.page_name="WELCOME "+this.username.toUpperCase();
     // this.comments=this.db.list('/comments').valueChanges();
     this.comments= this.db.list('/comments', ref => 
     ref.orderByChild('to').equalTo(this.username));

      this.items = this.comments.snapshotChanges().pipe(
      map(changes => 
        changes.map(c => ({ key: c.payload.key, ...c.payload.val() }))
      )
    );

  }
  delete(key: string){
   
    this.comments.remove(key);
  

  }

  change_sign(){
    this.page_name="Register";
    this.is_login=false;
  }
  change_login(){
    this.page_name="Login";
    this.is_login=true;
  }

  reg() {

    let loader = this.loadingCtrl.create({
      content: "Please wait...",
     // duration: 3000
    });
    loader.present();
    if(!this.username||!this.password){

      let alert = this.alertCtrl.create({
        title: "please enter username and password",
        buttons: ['OK']
      });
      alert.present();
  
      loader.dismiss();

    }

    else{

    this.username=this.username.toLowerCase();

    let user=this.username+"@abc.com";
    
    this.afAuth.auth.createUserAndRetrieveDataWithEmailAndPassword(user, this.password).then(result => {
      console.log('result', result)
      let user={name:this.username}
      this.db.list('/users').push(user).then(saved => {
        console.log('saved', saved);
        window.localStorage.setItem('isuser', this.username);
        this.display();
      }, error => {
        console.log('error', error)
      })
      loader.dismiss();
  

    }, error => {

      var msg;

      if(this.password.length<6){
        msg="please enter a password with min 6 char"
      }
      else{
        msg="username exists please select a different name";
      }
     
      let alert = this.alertCtrl.create({
        title: msg,
        buttons: ['OK']
      });
      alert.present();
  
      loader.dismiss();
    })
  }


  }

   
   
  
  onLogin(){
    if(!this.username||!this.password){

      let alert = this.alertCtrl.create({
        title: "please enter username and password",
        buttons: ['OK']
      });
      alert.present();

    }

    else{

     let loader = this.loadingCtrl.create({
      content: "Please wait...",
     // duration: 3000
    });
    loader.present();

    let user=this.username+"@abc.com";

    this.username=this.username.toLowerCase();
    

    this.afAuth.auth.signInAndRetrieveDataWithEmailAndPassword(user,this.password).then(result=> {
      console.log('result', result);
      this.display();
      window.localStorage.setItem('isuser', this.username);
      loader.dismiss();

    }, error => {
      console.log('error', error)
      let alert = this.alertCtrl.create({
        title: error,
        buttons: ['OK']
      });
      alert.present();
      loader.dismiss();
    })
    
    
  }
          
  }
  logout(){

    window.localStorage.removeItem('isuser');
    this.is_login = true;
    this.user_found=false;
    this.page_name="Login";

  }



  
  
  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterPage');

    if (this.username=window.localStorage.getItem('isuser')) {

      this.display();

    }

  }



}



