import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { AngularFireDatabase } from '@angular/fire/database';

@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage {
  msg;
  to;

  constructor(public navCtrl: NavController,public db: AngularFireDatabase, public alertCtrl: AlertController) {

  }
  swipe(event) {
    if(event.direction === 2) {
      this.navCtrl.parent.select(2);
    }
    if(event.direction === 4) {
      this.navCtrl.parent.select(0);
    }
  }
  
  send()
  {
    if(!this.to||!this.msg){

      let alert = this.alertCtrl.create({
        title: "Please enter the user name and the comment",
        buttons: ['OK']
      });
      alert.present();


    }
	else{

    this.to=this.to.toLowerCase();
     
    let comment={to:this.to,msg:this.msg};
    this.db.list('/comments').push(comment).then(saved => {
            console.log('saved', saved)
            this.to='';
            this.msg='';
          }, error => {
            console.log('error', error)
          })

           let alert = this.alertCtrl.create({
        title: "Your comment is send sucessfully",
        buttons: ['OK']
      });
      alert.present();

    

    
  }
  }

}



